html, body {
  box-sizing: border-box;
  color: #060607;
  font: normal 125%/1.6 Dosis, sans-serif;
  height: 100%;
  margin: 0;
  padding: 0;
}

html.admin-bar {
  height: calc(100% - 32px);
}
@media only screen and (max-width: 782px) {
  html.admin-bar {
    height: calc(100% - 46px);
  }
}

*, *::before, *::after {
  box-sizing: inherit;
}

a:link, a:visited {
  color: #5d2189;
}
a:active, a:focus, a:hover {
  color: #8c6c0c;
}

body {
  font-size: 1.125rem;
  margin: 0 auto;
  max-width: 800px;
  width: 90%;
}
body .sticky-footer-pusher {
  height: 100%;
}
body .sticky-footer-container {
  display: grid;
  grid-template-rows: auto 1fr auto;
  min-height: 100%;
}
@media only screen and (min-width: 768px) {
  body {
    padding-inline: min(10%, 13.75rem);
  }
}

ul.menu {
  list-style-type: none;
  margin: 0;
  padding: 0;
}

.hidden {
  display: none;
  visibility: hidden;
}

header.banner {
  text-align: center;
}
header.banner #page-title {
  font-family: "Libre Bodoni", serif;
  font-size: 2rem;
  font-weight: normal;
  margin: 0;
  padding-block-start: 2rem;
}
header.banner #page-title span {
  display: none;
}
@media only screen and (min-width: 500px) {
  header.banner #page-title span {
    display: inline;
  }
}
header.banner svg {
  cursor: pointer;
}
@media only screen and (min-width: 768px) {
  header.banner svg {
    display: none;
  }
}
header.banner .menu-container {
  display: grid;
  grid-template-rows: 0fr;
  transition: grid-template-rows 0.5s ease-in-out;
}
header.banner .menu-container .menu {
  font-size: 0;
  overflow: hidden;
  text-transform: uppercase;
}
header.banner .menu-container .menu-item-container {
  padding-block: 0.125rem;
}
header.banner .menu-container .menu-item {
  color: #060607;
  font-size: 0.875rem;
  text-decoration: none;
}
header.banner .menu-container .menu-item:active, header.banner .menu-container .menu-item:focus, header.banner .menu-container .menu-item:hover {
  text-decoration: underline;
}
@media only screen and (min-width: 768px) {
  header.banner .menu-container {
    display: block;
  }
  header.banner .menu-container .menu-item-container {
    display: inline-block;
    padding-inline: 0.5rem;
    position: relative;
  }
  header.banner .menu-container .menu-item-container::after {
    content: "•";
    font-size: 0.875rem;
    position: absolute;
    right: -3.25px;
  }
  header.banner .menu-container .menu-item-container:last-child::after {
    content: "";
  }
}
header.banner #menu-toggle + label .closer {
  display: none;
}
header.banner #menu-toggle:checked + label .opener {
  display: none;
}
header.banner #menu-toggle:checked + label .closer {
  display: inline;
}
header.banner #menu-toggle:checked ~ .menu-container {
  grid-template-rows: 1fr;
}

#content-container {
  margin-top: 2rem;
}

body.home .highlight {
  background: #3b3c53;
  border-radius: 50px;
  color: #ffffff;
  display: grid;
  grid-template-columns: 47.5% 47.5%;
  justify-content: space-between;
  padding: 50px;
  position: relative;
  right: 25px;
  width: calc(100% + 50px);
}
body.home .highlight * {
  margin-block-start: 0;
}
body.home .highlight figure {
  margin-inline: 0;
}
body.home .highlight img {
  max-width: 100%;
}

footer.site-footer {
  font-size: 0.75rem;
  text-align: center;
}

/*!
 * WP Core CSS
 * ===========
 * Use with _mixins.scss imported prior to this file
 */
.alignnone {
  margin: 0 1.25rem 1.25rem 0;
}

.aligncenter {
  clear: both;
  display: block;
  margin: 0.3125rem auto;
}

.alignright {
  float: right;
  margin: 0.3125rem 0 1.25rem 1.25rem;
}

.alignleft {
  float: left;
  margin: 0.3125rem 1.25rem 1.25rem 0;
}

a img.alignright {
  float: right;
  margin: 0.3125rem 0 1.25rem 20px;
}

a img.alignnone {
  margin: 0.3125rem 1.25rem 1.25rem 0;
}

a img.alignleft {
  float: left;
  margin: 0.3125rem 1.25rem 1.25rem 0;
}

a img.aligncenter {
  display: block;
  margin-left: auto;
  margin-right: auto;
}

.wp-caption {
  background: #fff;
  border: 1px solid #f0f0f0;
  max-width: 96%; /* Image does not overflow the content area */
  padding: 0.3125rem 3px 10px;
  text-align: center;
}

.wp-caption.alignnone {
  margin: 0.3125rem 1.25rem 1.25rem 0;
}

.wp-caption.alignleft {
  margin: 0.3125rem 1.25rem 1.25rem 0;
}

.wp-caption.alignright {
  margin: 0.3125rem 0 1.25rem 1.25rem;
}

.wp-caption img {
  border: 0 none;
  height: auto;
  margin: 0;
  max-width: 98.5%;
  padding: 0;
  width: auto;
}

.wp-caption p.wp-caption-text {
  font-size: 0.6875rem;
  line-height: 1.0625rem;
  margin: 0;
  padding: 0 0.25rem 0.3125rem;
}